body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.MuiTypography-root-1{
    padding: 1em;
}
.MuiTypography-body1-10{
    color: #1de9b6 !important;
}
.settings {
    display: grid;
    height: 535px;
    width: 200px;
    padding: 2em;
    border-radius: 5%;
}
.imgStyle{
    width: 300px;
    height: 300px;
    cursor: pointer;
}
.nameStyle{
    height: 20px;
    border-radius: 5%;
}
.buttonContainer{
    padding: 1em;
}
.buttonOnSettings{
    margin: 5px;
    padding: 1em;
}
.cursorPointer{
    cursor: pointer;
}
.buttonImage{
    padding: 1em;
    text-align: center;
}
.App {
  text-align: center;
  height: inherit;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#root{
      font-family: "Lato";
    display: -webkit-flex;
    display: flex;
    font-weight: 300;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100vw;
    height: 100vh;
    -webkit-box-align: center;
    -webkit-box-pack: center;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    background: #171919;
  background:-webkit-linear-gradient(top, #6589b1 0%, #32393c 100%);
}
